.login {
    display: flex;
    align-items: center;
    justify-content: center;

    .base-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-content: center;
        align-items: center;

        .header {
            margin-top: 25px;
            font-size: 24px;
            font-family: "Open Sans", sans-serif;
        }

        .content {
            display: flex;
            flex-direction: column;
            align-items: center;

            .image {
                width: 15em;
                img {
                    width: 100%;
                    height: 100%;
                }
            }

            .form {
                margin-top: 2em;
                display: flex;
                flex-direction: column;
                align-items: center;

                .form-group {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    width: fit-content;
                    label {
                        font-size: 20px;
                    }
                    input {
                        margin-top: 6px;
                        min-width: 18em;
                        height: 37px;
                        padding: 0px;
                        font-size: 16px;
                        font-family: "Open Sans", sans-serif;
                        background-color: #f3f3f3;
                        border: 0;
                        border-radius: 4px;
                        margin-bottom: 15px;
                        transition: all 250ms ease-in-out;
                        &:focus {
                            outline: none;
                            box-shadow: 0px 0px 12px 0.8px #0e81ce96;
                        }
                    }
                }
            }
        }
        .footer {
            margin-top: 1em;
        }
        .btn {
            font-size: 21px;
            padding: 5px 20px;
            border: 0;
            background-color: #1A535C;
            color: #fff;
            border-radius: 3px;
            transition: all 250ms ease-in-out;
            cursor: pointer;
            &:hover {
                background-color: #2386c8;
            }
            &:focus {
                outline: none;
            }
        }

        .errorMessage {
            margin-top: 1px;
            color: red;
        }
    }
}
