@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.addSpends{margin:1em;background-color:#4ecdc4;text-align:center;font-size:24px;border-radius:20px;padding:1em;color:#f7fff7;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);display:flex;align-items:center;justify-content:center;flex-direction:column}.addSpends .form-group{display:flex;align-items:center;justify-content:center;align-content:center;flex-direction:column;padding-top:1em;font-size:24px;color:black}.addSpends .form-group input{width:90%;margin-top:6px;height:37px;text-align:center;padding:0px;font-size:16px;font-family:"Open Sans", sans-serif;background-color:#f3f3f3;border:0;border-radius:9px;margin-bottom:15px;transition:all 250ms ease-in-out}.addSpends .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #0e81ce96}.addSpends .form-group .errorMessage{margin-top:1px;color:red}.addSpends footer{margin-top:1em}.addSpends footer .btn-inside{font-size:21px;padding:5px 20px;border:0;background-color:#1A535C;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.addSpends footer .btn-inside:hover{background-color:#349090}.addSpends footer .btn-inside:focus{outline:none}

.refresh{margin:0.5em}.refresh img{width:1em;height:1em}.refresh img:hover{-webkit-animation:spin 4s linear infinite;animation:spin 4s linear infinite}@-webkit-keyframes spin{100%{-webkit-transform:rotate(360deg)}}@keyframes spin{100%{transform:rotate(360deg)}}

.weekdata{background-color:#f7fff7;border:1px;border-color:#ff6b6b;border-width:20px;border-radius:20px;color:#FF6B6B;display:flex;flex-direction:row;padding:2%;align-items:center;align-content:center;justify-content:space-evenly;padding:0px;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);margin:.5em}.weekdata .spendImg{width:10%;border:3px solid #1a535c;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:50%;margin:3px;padding:0.5em}.weekdata .spendImg img{max-width:100%}.weekdata .spend .titleSpend{color:#4ECDC4}

.weekPanel{margin:1em;background-color:#4ecdc4;text-align:center;font-size:24px;border-radius:20px;padding:1em;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);display:flex;align-items:center;justify-content:center;flex-direction:column}.weekPanel header{display:flex;flex-direction:row;align-items:center;justify-content:space-around}.weekPanel .title{color:#F7FFF7}.weekPanel .container{margin:.5em}

.spend-content{background-color:#f7fff7;border:1px;border-color:#ff6b6b;border-width:20px;border-radius:20px;color:#FF6B6B;display:flex;flex-direction:column;padding:2%;align-items:center;align-content:center;justify-content:space-evenly;padding:0px;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);margin:.5em;padding:.5em;width:90%}.spend-content .spendTitle{color:#1A535C}.spend-content .dateSpend{color:#1A535C}.spend-content .spendDetail{color:#4ECDC4}

.month-content{margin:1em;background-color:#4ecdc4;text-align:center;font-size:24px;border-radius:20px;padding:1em;color:#f7fff7;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);display:flex;align-items:center;justify-content:center;flex-direction:column}.month-content header{margin-bottom:0.5em}.month-content footer{margin-top:0.5em}.month-content main{display:flex;flex-direction:column;justify-content:center;align-items:center;max-height:700px;overflow-y:scroll}.month-content main .principal{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;max-height:100%}

.home-container{display:flex;flex-direction:column;align-items:center;justify-content:space-around}.home-container .header-home{display:flex;align-items:center;justify-content:space-between;width:100%}.home-container .main-content{width:100%;height:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-around}.home-container .main-content .right-content{max-width:20%;flex-grow:1}.home-container .main-content .content{flex-grow:2;display:flex;align-items:center;flex-direction:row;align-items:center;justify-content:space-between;max-height:860px}.home-container .main-content .content .summary{width:100%}

.login{display:flex;align-items:center;justify-content:center}.login .base-container{width:100%;display:flex;flex-direction:column;align-content:center;align-items:center}.login .base-container .header{margin-top:25px;font-size:24px;font-family:"Open Sans", sans-serif}.login .base-container .content{display:flex;flex-direction:column;align-items:center}.login .base-container .content .image{width:15em}.login .base-container .content .image img{width:100%;height:100%}.login .base-container .content .form{margin-top:2em;display:flex;flex-direction:column;align-items:center}.login .base-container .content .form .form-group{display:flex;flex-direction:column;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.login .base-container .content .form .form-group label{font-size:20px}.login .base-container .content .form .form-group input{margin-top:6px;min-width:18em;height:37px;padding:0px;font-size:16px;font-family:"Open Sans", sans-serif;background-color:#f3f3f3;border:0;border-radius:4px;margin-bottom:15px;transition:all 250ms ease-in-out}.login .base-container .content .form .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #0e81ce96}.login .base-container .footer{margin-top:1em}.login .base-container .btn{font-size:21px;padding:5px 20px;border:0;background-color:#1A535C;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.login .base-container .btn:hover{background-color:#2386c8}.login .base-container .btn:focus{outline:none}.login .base-container .errorMessage{margin-top:1px;color:red}

.App{text-align:center;display:flex;justify-content:center;align-items:center;font-family:"Open Sans", sans-serif}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.login{width:27em;height:40em;display:flex;justify-content:center;align-items:center;margin-top:5px;position:relative;z-index:99}.login .container{display:flex;justify-content:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:100%;z-index:99;padding:22px 10px}.login .right-side{display:flex;flex-direction:column;justify-content:center;height:90%;background-color:#3498db;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{right:-40%;align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}.btn{font-size:21px;padding:5px 20px;border:0;background-color:#3498db;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#2386c8}.btn:focus{outline:none}

